.promo__wrapper {
  max-width: 1160px;
  margin: 0 auto;
  padding: 120px 0;
}

.promo__title {
  margin: 0 0 70px 0;
  text-align: center;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 1.62px;
}

.promo__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}

.promo__item {
  &--primary {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row: span 2;

    & h3 {
      margin: 0 0 15px 0;
      font-size: 26px;
      font-weight: 700;
      line-height: 1;
    }

    & span {
      margin-bottom: 25px;
      font-size: 70px;
      font-weight: 700;
      line-height: 1;
    }

    & p {
      flex-grow: 1;
      margin: 0;
      font-size: 18px;
      line-height: 1.33;
      font-weight: 300;
    }

    & a {
      color: inherit;

      &::after {
        background-color: $color-default-white;
        bottom: 6px;
      }

      &:not([disabled]):focus {
        outline: 1px solid $color-default-white;
      }

      &[disabled] {
        opacity: 0.5;
      }
    }
  }

  &--secondary {
    display: flex;
    flex-direction: column;
    min-height: 211px;
    padding: 23px 28px 23px;
    border: 7px solid;
    border-image: $gradient-border-tickets-promo;
    border-image-slice: 1;

    & h3 {
      margin: 0 0 10px 0;
      font-size: 26px;
      font-weight: 700;
      line-height: 1;
    }

    & p {
      margin: 0;
      flex-grow: 1;
      font-weight: 300;
      line-height: 1.5;
      letter-spacing: -0.15px;
    }

    & a {
      color: $color-resolution-blue;

      &::after {
        background-color: $color-resolution-blue;
        bottom: 5px;
      }

      &:not([disabled]):focus {
        outline: 1px solid $color-sapphire;
      }

      &[disabled] {
        opacity: 0.3;
      }
    }
  }
}

.promo__item-description {
  display: flex;
  flex-direction: column;
  padding: 40px 46px 39px 45px;
  background-color: $color-torch-red-first;
  color: $color-default-white;
}

.promo__item-image {
  background-color: $color-dark-cerulean-50;
}

.promo__item-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.promo__item-link {
  font-size: 14px;
  position: relative;
  width: max-content;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  padding: 10px;
  margin-left: -10px;
  letter-spacing: 0.7px;

  &::after {
    content: "";
    position: absolute;
    width: calc(100% - 20px);
    left: 10px;
    height: 1px;
  }

  &:not([disabled]):focus {
    &::after {
      display: none;
    }
  }

  &:not([disabled]):active {
    outline: $color-transparent;
    position: relative;

    &::after {
      display: block;
    }
  }

  @include hover {
    opacity: 80%;
  }

  &[disabled] {
    pointer-events: none;
    cursor: default;
  }
}
